import React from 'react'
import { FaReact} from 'react-icons/fa'



function DataCard(props) {
    return (
       <>
       <div className="data_card pb-2 pt-2" style={{backgroundColor: props.color}}><h5 className="text-center">{props.title}</h5><h3>{props.value}</h3></div>
       </>
    )
}

export default DataCard
